import React from 'react';
import GoogleLogin from 'react-google-login';
import './gmailLogin.css';
import { NavLink, useHistory } from "react-router-dom";


export const GmailLogin = () => {
    const addToLocalStorage=(data)=>{
        const contacts={
            "fName": data.Ts.RT[0].toUpperCase()+data.Ts.RT.substring(1).toLowerCase() +data.Ts.TR[0].toUpperCase()+data.Ts.TR.substring(1).toLowerCase(),
            "emailID":data.Ts.Et,
            "emailLogin":true,
            "mobileValidation":false

        }
        console.log(contacts);
        localStorage.setItem('contacts',JSON.stringify(contacts));
    }
    
    const history = useHistory();
    const responseGoogle = (response) => {
        console.log(response);
        if (response.error === "popup_closed_by_user")
            console.log("Don't close the pop up window or if same error occur again then clear cache memory and trylogging in again");
        console.log(response.Ts.Ne)
        addToLocalStorage(response);
        history.push("/mobile_verification");
    }
    return (
        <div className='gmaillogin'>
            <h1>Login with Gmail </h1>
            <GoogleLogin className="gmailloginButton"
                clientId="802066088065-ogkll96k3ciimk7jhkp85pps3otfp05r.apps.googleusercontent.com"
                buttonText="Login"
                onSuccess={responseGoogle}
                onFailure={responseGoogle}
                cookiePolicy={'single_host_origin'}
            />
            <NavLink className="facebookButton" to="/loginfacebook" activeClassName="selected">Login with facebook</NavLink>
        </div>
    )
}
