import React, { useEffect, useRef,useState } from 'react';
import { useHistory } from 'react-router-dom';
import firebase from '../components/firebase';
import "./mobileVerification.css";

var contacts=JSON.parse(localStorage.getItem('contacts'));
const updateLocalStorage=()=>{
    contacts.mobileValidation=true;
    localStorage.setItem('contacts',JSON.stringify(contacts));
}
export const MobileVerification = () => {
    const [flag, setflag] = useState(true)
    useEffect(()=>{
        contacts=JSON.parse(localStorage.getItem('contacts'));
        setflag(!flag);
    })
    const history=useHistory();
    const mobileNumeber = useRef('')
    const handleClick=()=>{
        var recaptcha= new firebase.auth.RecaptchaVerifier('recaptcha');
        var number=mobileNumeber.current.value;
        firebase.auth().signInWithPhoneNumber(number,recaptcha).then((e)=>{
            var code=prompt('enter the otp','');
            if(code===null) return;
           e.confirm(code).then((result)=>{
            console.log(result.user,'user');
            document.querySelector('label').innerText=result.user.phoneNumber +'Number Verified';
            updateLocalStorage();
                history.push('/dashboard');
           }).catch((error)=>console.log("hahaah",error)) 

        }).catch((error)=>console.log("haha",error))
    }
   if(contacts===null) return (<div>
    <h1>Please Verify Email</h1>
    <button onClick={()=>history.push('/login')}>Click here for Email Verification</button>
</div>)
   else if(contacts.emailLogin===true)return (
        <div className="base">
            <h1>Verify your Mobile Number Here</h1>
            <h3>Please Enter Mobile Number with country code </h3>
            <input type="text" ref={mobileNumeber} placeholder="Example +919876543210"/>
            <label></label>
            <div id='recaptcha'></div>
            <button onClick={handleClick}>Send OTP</button>
        </div>
    )
    else return (<div>
        <h1>Please Verify Email</h1>
        <button onClick={()=>history.push('/login')}>Click here for Email Verification</button>
    </div>)
}
