import './App.css'; 
import {
  BrowserRouter,
  Switch,
  Route,
} from "react-router-dom";
import { GmailLogin } from './pages/GmailLogin';
import { Header } from './components/header';
import { FacebookLoginPage } from './pages/FacebookLogin';
import { MobileVerification } from './pages/Mobile_Verification';
import { Dashboard } from './pages/Dashboard';

function App() {
  return (
    <BrowserRouter>
    
      <div>
        <Switch>{/* switch and route */}
          <Route path="/login">
          <GmailLogin/>
          </Route>
          <Route path="/loginfacebook">
           <FacebookLoginPage/>
          </Route>
          <Route path="/mobile_verification">
            <MobileVerification/>
          </Route>
          <Route path="/dashboard">
          <Dashboard/>
          </Route>
          <Route>
            <h1>Wrong Url go to /login</h1>
          </Route>


        </Switch>
      </div>
    </BrowserRouter>
  );
}

export default App;
