import React from 'react';
import './gmailLogin.css';
import { NavLink, useHistory } from "react-router-dom";
import FacebookLogin from 'react-facebook-login';

export const FacebookLoginPage = () => {
    const addToLocalStorage=(data)=>{
        const contacts={
            "fName": data.name,
            "emailID":data.email,
            "emailLogin":true,
            "mobileValidation":false

        }
        console.log(contacts);
        localStorage.setItem('contacts',JSON.stringify(contacts));
    }
    const history=useHistory();
const responseFacebook = (response) => {
    console.log(response);
    console.log(response.name);
    addToLocalStorage(response);
    history.push("/mobile_verification");
}
const componentClicked=(data)=>{
    console.log(data);
}
    return (
        <div className='gmaillogin'>
            <h1>Login with facebook </h1>
            <FacebookLogin
                appId="883274949281896"
                autoLoad={true}
                fields="name,email,picture"
                onClick={componentClicked}
                callback={responseFacebook} />



            <NavLink className="gmailButton" to="/login" activeClassName="selected">Login with Gmail</NavLink>
        </div>
    )
}
